@charset "utf-8";

.advantage_top{
    display: flex;
    align-items: end;
    padding: 140px 0;
    gap: 100px;
    justify-content: space-between;
    width: 88vw;
}
.sec_title {
  margin-top: 40px;
}
.advantage_text{
    margin-top: 80px;
    line-height: 2.5;
}
.advantage_top_img{
    width: 200vw;
}
.advantage_top_img img{
    height: 600px;
    width: 100%;
    object-fit: cover;
}
.advantage_flex{
    display: flex;
    justify-content: space-between;
    margin-bottom: 80px;
    gap: 50px;
}
.warm,.relaxed{
    width: 50%;
}
.interior_color {
  display: flex;
  align-items: end;
  gap: 40px;
}
.interior_color img{
    width: 285px
}
.interior_text {
  line-height: 2;
}
.color_title{
    text-align: center;
    padding: 3px 0;
    width: 200px;
    border: solid 1px#000;
}
.color_scheme {
  text-align: center;
  font-size: 20px;
  margin: 20px 0 10px;
  letter-spacing: 2px;
}
.color_box {
  color: #fff;
  padding: 40px 10px 10px;
  font-size: 16px;
  font-weight: 100;
  position: relative;
  line-height: 1.2;
}
.warm_color1 {
  background-color: #ad9a88;
  margin-bottom: 10px;
}
.warm_color2 {
  background-color: #c7a494;
  width: 60%;
}
.relaxed_color1 {
  background-color: #4d5357;
  margin-bottom: 10px;
}
.relaxed_color2 {
  background-color: #43403b;
  width: 60%;
}
.sofa_color {
  background-color: #d8d7d6;
  width: 40%;
}
.sofa_color p {
  position: absolute;
  bottom: 10px;
  left: 10px;
}
.color_flex {
  display: flex;
  gap: 10px;
}
.interior_story {
  font-size: 20px;
  padding: 40px 0 20px;
  letter-spacing: 5px;
}
/* tab */
@media (max-width: 1200px) {
    .advantage_top{
        width: 94vw;
        gap: 50px;
    }
    .advantage_top_img{
        width: 270vw;
    }
    .interior_color{
        gap: 20px;
    }
    .interior_color img{
        width: 40%;
    }
}

@media (max-width: 768px) {
    .advantage_top{
        flex-flow: column;
        align-items: start;
        padding: 85px 0 55px;
        gap: 50px;
        justify-content: start;
        width: 100%;
    }
    .advantage_top_img{
        width: 95vw;
    }
    .advantage_top_img img{
        height: auto;
        width: 100%;
    }
    .sec_title{
        margin-top: 35px;
    }
    .advantage_text
    {
        margin-top: 0;
        line-height: 2;
    }
    .advantage_flex{
        flex-flow: column;
        justify-content: start;
        margin-bottom: 30px;
    }
    .warm,.relaxed{
        width: 100%;
    }
    .interior_color{
        gap: 10px;
    }
    .interior_color img{
        width: 180px
    }
    .interior_text{
        line-height: 1.8;
    }
    .color_title{
        width: 150px;
    }
    .color_scheme{
        font-size: 18px;
        margin: 15px 0 10px;
        letter-spacing: 0px;
    }
    .interior_story{
        font-size: 18px;
        padding: 30px 0 10px;
        letter-spacing: 5px;
    }
}
