/*----------------------------
    company_name 
    流れる会社名
----------------------------*/

.company_name {
  font-size: 136px;
  color: #efe3e0;
  letter-spacing: 13px;
  display: flex;
  overflow: hidden;
  align-items: center;
  width: 100%;
  white-space: nowrap;
  z-index: 1;
}
.company_name ul {
  animation: flowing 100s linear infinite;
  transform: translateX(100%);
  margin: 0;
  padding: 0;
}
.company_name ul li {
  display: inline-block;
  padding-right: 30px;
}
@keyframes flowing {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-100%);
  }
}

.and_text{
    font-size: 85px;
}
@media (max-width: 767px) {
    .company_name{
        font-size: 70px;
    }
}

/*----------------------------
    Responsive 
    レスポンシブ
----------------------------*/

/* スクリーン幅が768px未満のデバイスでのみ表示 */
.mobile-only{
    display: none;
}
/* スクリーン幅が768pxより大きいデバイスでのみ表示 */
.pc-only{
    display: block;
}

@media(max-width:768px){
    .mobile-only{
        display: block;
    }

    .pc-only{
        display: none;
    }
}

/*----------------------------
    Main Grid
    メイングリッド tab〜mobile
----------------------------*/
@media (max-width: 1200px) {
    .main-grid{
        display: grid;
        grid-template-columns: 1fr 90% 1fr;
    }
}
