#service {
  background-color: #efe3e0;
  padding: 140px 0;
  text-align: center;
}

#service h2 {
  margin-top: 28px;
}

.service_text {
  line-height: 28px;
  letter-spacing: 1px;
  margin-top: 32px;
}

.service_flex {
  display: flex;
  justify-content: space-between;
}

.service_item {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.service_item {
  margin-top: 40px;
}

.service_item img {
  width: 230px;
}

.service_item h3 {
  z-index: 1;
  margin-top: 10px;
  letter-spacing: 3.5px;
}

.service_item p {
  text-align: center;
  width: 340px;
  height: 110px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  margin-top: -23px;
  line-height: 28px;
  letter-spacing: 1px;
}

.arrow_down {
  display: flex;
  justify-content: center;
  padding-top: 40px;
}

.arrow_down img {
  width: 53px;
  display: block;
}

.plus {
  display: inline-block;
  vertical-align: middle;
  color: #c38c80;
  line-height: 1;
  width: 2px;
  height: 40px;
  background: currentColor;
  border-radius: 0;
  position: relative;
  margin-top: 135px;
}

.plus::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: inherit;
  border-radius: inherit;
  transform: rotate(90deg);
}
