@charset "utf-8";

.gallery_content {
  margin: 50px 0 140px;
}
.gallery_top {
  display: flex;
  align-items: end;
  justify-content: space-between;
  margin-bottom: 70px;
}
.follow_btn {
  display: flex;
  align-items: end;
  gap: 15px;
}
.follow_btn p {
  padding-bottom: 5px;
  border-bottom: solid #000 1px;
  letter-spacing: 2px;
}
.follow_btn p:hover {
  color: #c38c80;
  transition: 0.3s;
  padding-bottom: 2px;
}
.follow_btn img {
  width: 30px;
}
